body.portfolio {
  --bg-color: #000;
  background-color: var(--bg-color);
  font-family: "neurimboGothicRegular";
  font-size: 20px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

::-webkit-scrollbar {
  display: none;
}

.hdbox {
  height: 80px;
}

.main_animation {
  position: fixed;
  width: 100vw;
  height: 100vh;
  z-index: 99999;
  background: #000;
  animation: roding 2s ease-in forwards;
  display: block;
}
@-webkit-keyframes roding {
  70% {
    opacity: 1;
  }
  90% {
    display: block;
  }
  100% {
    opacity: 0;
    display: none;
  }
}
@-moz-keyframes roding {
  70% {
    opacity: 1;
  }
  90% {
    display: block;
  }
  100% {
    opacity: 0;
    display: none;
  }
}
@keyframes roding {
  70% {
    opacity: 1;
  }
  90% {
    display: block;
  }
  100% {
    opacity: 0;
    display: none;
  }
}
.main_animation .rotate_text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.main_animation .rotate_text svg {
  animation: main_animation 20s linear forwards;
}
@-webkit-keyframes main_animation {
  0% {
    transform: rotate(-180deg);
  }
  6% {
    transform: rotate(-60deg);
    opacity: 1;
  }
  10% {
    transform: rotate(0);
    opacity: 0;
  }
}
@-moz-keyframes main_animation {
  0% {
    transform: rotate(-180deg);
  }
  6% {
    transform: rotate(-60deg);
    opacity: 1;
  }
  10% {
    transform: rotate(0);
    opacity: 0;
  }
}
@keyframes main_animation {
  0% {
    transform: rotate(-180deg);
  }
  6% {
    transform: rotate(-60deg);
    opacity: 1;
  }
  10% {
    transform: rotate(0);
    opacity: 0;
  }
}

