header {
  width: 100%;
  height: 50px;
}
header .hd_fixed.fixed {
  width: 100%;
  height: 80px;
  top: 0px;
  z-index: 999;
  display: flex;
  align-items: center;
  mix-blend-mode: difference;
  position: fixed;
  backdrop-filter: blur(10px);
  border-bottom: 1px solid white;
  transform: translateY(0px);
}
header .hd_fixed.fixed svg path {
  fill: #fff;
}
header .header_wrap {
  width: 1020px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  height: 50px;
  background: transparent;
  border: unset;
  padding: unset;
}
header .header_wrap .hd_right {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  position: relative;
  width: 412px;
}
header .header_wrap .hd_right .date {
  color: white;
  margin-right: 50px;
  font-size: 24px;
  border: 2px solid #fff;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  overflow: hidden;
  width: 212px;
  height: 48px;
  transition: 0.5s;
  border-radius: 40px;
}
header .header_wrap .hd_right .date p {
  color: white;
  font-size: 24px;
  padding: 8px 16px 12px;
  width: 212px;
  height: 48px;
  transition: 0.5s ease-in-out;
}
header .header_wrap .hd_right .date p.datep {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
header .header_wrap .hd_right .date p.datet {
  width: 192px;
  position: absolute;
  top: 50%;
  transform: translate(-192px, -50%);
  letter-spacing: 2px;
}
header .header_wrap .hd_right .date:hover {
  width: 192px;
}
header .header_wrap .hd_right .date:hover .datep {
  transform: translate(50%, -50%);
}
header .header_wrap .hd_right .date:hover .datet {
  transform: translate(0%, -50%);
}
header .header_wrap .hd_right .menuBtn {
  width: 50px;
  height: 50px;
  position: relative;
}
header .header_wrap .hd_right .menuBtn span {
  position: absolute;
  top: 50%;
  left: 50%;
  display: inline-block;
  width: 30px;
  height: 2px;
}
header .header_wrap .hd_right .menuBtn span.menuBtn_l1 {
  transform: translate(-50%, -4px);
}
header .header_wrap .hd_right .menuBtn span.menuBtn_l1::before {
  content: "";
  position: relative;
  width: 30px;
  height: 2px;
  background: #fff;
  display: block;
}
header .header_wrap .hd_right .menuBtn span.menuBtn_l2 {
  transform: translate(-50%, 4px);
  display: flex;
  justify-content: flex-end;
}
header .header_wrap .hd_right .menuBtn span.menuBtn_l2::before {
  content: "";
  position: relative;
  width: 18px;
  height: 2px;
  background: #fff;
  display: block;
  transition: width 360ms cubic-bezier(0.86, 0, 0.07, 1);
}
header .header_wrap .hd_right .menuBtn:hover .menuBtn_l2::before {
  width: 30px;
}
header .header_wrap .hd_right .menuBtn.view {
  flex-direction: row;
  width: 50px;
  height: 50px;
  position: relative;
}
header .header_wrap .hd_right .menuBtn.view span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
header .header_wrap .hd_right .menuBtn.view span:first-child {
  transform: translate(-50%, -50%) rotate(45deg);
}
header .header_wrap .hd_right .menuBtn.view span:last-child {
  transform: translate(-50%, -50%) rotate(-45deg);
}

