.contact {
  width: 100%;
  overflow: hidden;
  min-width: 1020px;
}
.contact .contact_wrap .contact_title {
  color: #fff;
  text-align: left;
  width: 100%;
  background: #ff3d00;
  padding: 100px 50px 100px 50px;
  border-radius: 40px;
  margin-bottom: 80px;
  max-width: 1020px;
  margin: 0 auto;
}
.contact .contact_wrap .contact_title .title {
  font-family: "neurimboGothicRegular";
  font-size: 168px;
  margin-bottom: 60px;
  text-align: right;
}
.contact .contact_wrap .contact_title .text {
  font-family: "Pretendard-Medium";
  font-size: 20px;
  line-height: 42px;
}
.contact .contact_wrap {
  --offset: 0px;
  --move-initial: calc(-33.333333% + var(--offset));
  --move-final: calc(-66.666666% + var(--offset));
  --marquee-width: 100vw;
}
.contact .contact_wrap .lineText {
  font-family: "neurimboGothicRegular";
  font-size: 250px;
  color: #fff;
  display: flex;
  width: fit-content;
  position: relative;
  transform: translateX(100%);
  animation: marquee 18s linear infinite;
  animation-play-state: running;
}
@-webkit-keyframes marquee {
  0% {
    transform: translate3d(var(--move-initial), 0, 0);
  }
  100% {
    transform: translate3d(var(--move-final), 0, 0);
  }
}
@-moz-keyframes marquee {
  0% {
    transform: translate3d(var(--move-initial), 0, 0);
  }
  100% {
    transform: translate3d(var(--move-final), 0, 0);
  }
}
@keyframes marquee {
  0% {
    transform: translate3d(var(--move-initial), 0, 0);
  }
  100% {
    transform: translate3d(var(--move-final), 0, 0);
  }
}
.contact .contact_wrap .lineText p {
  white-space: nowrap;
}
.contact .contact_wrap .contactBox {
  width: 1020px;
  height: 500px;
  margin: 150px auto 0;
  display: flex;
  justify-content: center;
  gap: 20px;
  text-align: left;
}
.contact .contact_wrap .contactBox .box {
  width: 500px;
  display: flex;
  justify-content: center;
}
.contact .contact_wrap .contactBox .box .button_wrap {
  margin: 129px 60px 0;
  width: 380px;
  max-height: 250px;
}
.contact .contact_wrap .contactBox .box .button_wrap p {
  margin: 20px 0;
  font-family: "Pretendard-Medium";
  font-size: 38px;
  color: rgba(255, 255, 255, 0.5);
  line-height: 48px;
}
.contact .contact_wrap .contactBox .box .button_wrap img {
  width: 64px;
  height: 74px;
}
.contact .contact_wrap .contactBox h2 {
  font-family: "neurimboGothicRegular";
  font-size: 45px;
  color: #fff;
}
.contact .contact_wrap .contactBox a {
  display: none;
}

