.fixedtbtn {
  width: 200px;
  height: 50px;
  border-radius: 50px;
  border: 2px solid #fff;
  background: rgba(255, 255, 255, 0.3);
  display: none;
  bottom: 25px;
  left: 50%;
  transform: translate(-50%, 0);
  position: fixed;
  z-index: 990;
  backdrop-filter: blur(5px);
  transition: all 900ms cubic-bezier(0.22, 1, 0.36, 1);
  overflow: hidden;
}
.fixedtbtn.show {
  display: flex;
}
.fixedtbtn .img1 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 0.5s;
}
.fixedtbtn .img2 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 120%);
  transition: 0.5s;
}
.fixedtbtn:hover {
  transform: translate(-50%, -5px);
}
.fixedtbtn:hover .img1 {
  transform: translate(-50%, -250%);
}
.fixedtbtn:hover .img2 {
  transform: translate(-50%, -50%);
}